<template>
  <div style="padding: 10px">

    <!--    搜索-->
    <div style="margin: 10px 0px;">
      <el-autocomplete
          v-model="search"
          :fetch-suggestions="querySearch"
          placeholder="请输入评阅状态"
          clearable style="width:20%;margin-left: 950px"
      >
        <template #default="{ item }">
          <div class="value">{{ item.value }}</div>
        </template>
      </el-autocomplete>
      <el-button type="primary" style="margin-left: 5px;" @click="load">查询</el-button>
      <el-button type="primary" style="margin-left: 5px;" @click="upload" v-show="isShow">上传</el-button>
    </div>
    <el-table :data="tableData" border style="width: 100%" stripe>
      <el-table-column prop="id" label="X光图ID" sortable/>
      <el-table-column prop="PName" label="病人姓名" />
      <el-table-column prop="SName" label="专家姓名" />
      <el-table-column label="X光图">
        <template #default="scope">
          <el-image
              style="width: 100px; height: 100px"
              :src="scope.row.img"
              :preview-src-list="[scope.row.img]"
              fit="cover"
              preview-teleported="true"
          />
        </template>
      </el-table-column>
      <el-table-column label="专家评阅结果">
        <template #default="scope">
          <el-button size="mini" @click="check(scope.row)">查看</el-button>
          <el-button size="mini" @click="downLoad(scope.row)">下载</el-button>
        </template>
      </el-table-column>
      <el-table-column prop="state" label="评阅状态" sortable/>
      <el-table-column prop="createTime" label="上传时间" sortable/>
      <el-table-column label="操作">
        <template #default="scope">
          <el-button size="mini" @click="handleEidt(scope.row)">编辑</el-button>
          <el-popconfirm title="你确认删除吗?" @confirm="handleDelete(scope.row.id)">
            <template #reference>
              <el-button size="mini" type="danger" v-show="!isS">删除</el-button>
            </template>
          </el-popconfirm>
        </template>
      </el-table-column>
    </el-table>

    <div style="margin: 10px 0px;float: right">
      <el-pagination
          v-model:currentPage="currentPage"
          :page-size="pageSize"
          :page-sizes="[5, 10, 20]"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
      />

      <el-dialog
          v-model="dialogVisible"
          title="上传或更新"
          width="30%"
      >
        <el-form :model="form" label-width="120px">
          <el-form-item label="X光图ID" v-show="isNotShow">
            <el-input v-model="form.id" style="width: 160px" disabled></el-input>
          </el-form-item>
          <el-form-item label="病人ID" v-show="isNotShow">
            <el-input v-model="form.pid" style="width: 160px" disabled></el-input>
          </el-form-item>
          <el-form-item label="请选择专家">
            <el-select v-model="form.SName" clearable @change="changeS"
                       size="small">
              <el-option
                  v-for="item in specialist"
                  :key="item.id"
                  :label="item.nickName"
                  :value="item.id">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="专家评阅结果" v-show="isNotShow">
            <el-input v-model="form.result" style="width: 160px" disabled></el-input>
          </el-form-item>
          <el-form-item label="评阅状态" v-show="isNotShow">
            <el-radio v-model="form.state" label="已评阅">已评阅</el-radio>
            <el-radio v-model="form.state" label="未评阅">未评阅</el-radio>
          </el-form-item>
          <el-form-item label="X光图" style="width: 300px">
            <el-upload
                ref="upload"
                :action = filesUploadUrl
                :on-success="fileUpload"
                style="width: 300px"
            >
              <el-button type="primary">上传</el-button>
            </el-upload>
          </el-form-item>
        </el-form>
        <template #footer>
          <span class="dialog-footer">
            <el-button @click="dialogVisible = false">取消</el-button>
            <el-button type="primary" @click="save">确定</el-button>
      </span>
        </template>
      </el-dialog>

      <el-dialog
          v-model="isShowResult"
          title="专家评阅结果"
          width="50%"
      >
        <el-input
            type="textarea"
            :rows="20"
            placeholder="请输入内容"
            v-model="result" disabled>
        </el-input>
      </el-dialog>


    </div>
  </div>
</template>

<script>

import request from "@/utils/request";


export default {
  name: "Img",
  components: {
  },
  data(){
    return{
      querySearch:[
        {value:'已评阅'},
        {value:'未评阅'},
      ],
      search:'',
      currentPage:1,
      pageSize:10,
      total:1,
      tableData:[],
      form: {},
      dialogVisible:false,
      isShowResult:false,
      user:{},
      isShow:false,
      isNotShow:true,
      specialist:[],
      patient:[],
      result:'',
      isS:false,
      isP:false,
      isA:false,
      filesUploadUrl:"http://" + window.server.filesUploadUrl + ":9090/files/upload",
    }
  },
  created() {
    let str = sessionStorage.getItem("user") || "{}"
    this.user = JSON.parse(str)
    let specialist = sessionStorage.getItem("specialist") || "{}"
    this.specialist = JSON.parse(specialist)
    let patient = sessionStorage.getItem("patient") || "{}"
    this.patient = JSON.parse(patient)
    if(this.user.role === '0'){
      this.isA = true
      this.isS = false
      this.isP = false
    }else if(this.user.role === '1'){
      this.isShow = true
      this.isNotShow = false

      this.isA = false
      this.isS = false
      this.isP = true
    }else{
      this.isA = false
      this.isS = true
      this.isP = false
    }
    this.load()
  },

  methods:{
    load(){
      this.tableData = []
      let sid = ''
      let pid = ''
      if(this.user.role === '0'){
        //管理员
        sid = ''
        pid = ''
      }else if(this.user.role === '1'){
        //病人
        sid = ''
        pid = this.user.id
      }else if(this.user.role === '2'){
        //专家
        sid = this.user.id
        pid = ''
      }
      request.get("/img",{
        params:{
          pageNum:this.currentPage,
          pageSize:this.pageSize,
          search:this.search,
          sid:sid,
          pid:pid
        }
      }).then(res => {
        this.tableData = res.data.records
        this.total = res.data.total
        this.tableData.forEach(td=>{
          this.specialist.forEach(s=>{
            if(td.sid == s.id){
              td.SName = s.nickName
            }
          })
          this.patient.forEach(p=>{
            if(td.pid == p.id){
              td.PName = p.nickName
            }
          })
        })
      })
    },

    fileUpload(res){
      this.form.img = res.data
    },

    save(){
      if(this.form.id){ //更新
        request.put("/img",this.form).then(res => {
          if(res.code === '0'){
            this.$message({
              type:"success",
              message:"更新成功"
            })
          }else{
            this.$message({
              type:"error",
              message:res.message
            })
          }
          this.load() //刷新表格
          this.dialogVisible = false
        })
      }else{ //新增
        request.post("/img",this.form).then(res => {
          if(res.code === '0'){
            this.$message({
              type:"success",
              message:"新增成功"
            })
          }else{
            this.$message({
              type:"error",
              message:res.message
            })
          }
          this.load() //刷新表格
          this.dialogVisible = false
        })
      }
    },

    handleSizeChange(val){ //改变每页个数
      this.pageSize = val
      this.load()
    },
    handleCurrentChange(val){  //改变当前页码
      this.currentPage = val
      this.load()
    },
    upload(){

      if(this.tableData!=""){
        this.form = this.tableData[0];
        this.form.id = ''
      }
      this.form.pid = this.user.id
      this.form.state = '未评阅'
      this.form.result = ''
      this.dialogVisible = true
      this.$refs['upload'].clearFiles() //清除历史文件列表
    },

    changeS(s){
      this.form.sid = s
    },

    check(row){
      this.isShowResult = true
      this.result = row.result
    },

    downLoad(row){
      if(row.state == "未评阅"){
        this.$alert('该脊椎X光图专家还未评阅，请耐心等待，谢谢', '提示', {
          confirmButtonText: '确定',
          type:"warning"
        });
        return false
      }
      if(row.result == ""){
        this.$alert('该脊椎X光图专家还未曾给出建议，请耐心等待，谢谢', '提示', {
          confirmButtonText: '确定',
          type:"warning"
        });
        return false
      }
      this.form = JSON.parse(JSON.stringify(row))
      let flag = ''
      request.post("/files/integration",this.form).then(res => {
        flag = res.data
        let url = "http://175.24.204.121:9090/files/"+flag
        window.open(url)
      })
    },

    handleEidt(row){
      this.form = JSON.parse(JSON.stringify(row))
      if(this.user.role === '0'){
        this.$nextTick(() =>{
          this.$refs['upload'].clearFiles() //清除历史文件列表
        })
        //管理员
        this.dialogVisible = true
      }else if(this.user.role === '1'){
        this.$nextTick(() =>{
          this.$refs['upload'].clearFiles() //清除历史文件列表
        })
        //病人
        this.dialogVisible = true
      }else if(this.user.role === '2'){
        //专家
        sessionStorage.setItem("img",JSON.stringify(row))
        this.$router.push("/canvas")
      }
    },
    handleDelete(id){
      request.delete("/img/"+id).then(res =>{
        if(res.code === '0'){
          this.$message({
            type:"success",
            message:"删除成功"
          })
        }else{
          this.$message({
            type:"error",
            message:res.message
          })
        }
        this.load() //刷新表格
      })
    },





  },

}
</script>

<style scoped>

</style>
